.firstLine {
    font-weight: bold;
}

.notFirstLine {
    font-size: calc(10px + 1vmin);
}

.lastLine {
    font-size: calc(5px + 1vmin);;
}

.singleCell{
    margin-bottom: 100px;
    width: 75vw;
    max-width: 750px;
}

.notFirstLine{
    padding-left: 20px;
    padding-right: 20px;
    text-align: left;
}

a {
    color: lightblue;
    text-decoration: none;
}

a:hover {
    color: lightcyan
}

a:click{
    color: darkblue;
}